.icon-cube {
position: relative;
-webkit-perspective: 100px;
 perspective: 100px;
overflow: visible;
}

/*-- agileits --*/
.icon-cube::before,
.icon-cube::after {
display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: FontAwesome;
  font-size: 1em;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  line-height: 2.6em;
}

.icon-cube::before {
z-index: 2;
background-color:#fff;
}

.icon-cube::after {
z-index: 1;
opacity: 0;
-webkit-transform:translateY(25px) rotateX(-90deg);
  transform:translateY(25px) rotateX(-90deg);
}

.icon-cube:hover::before {
opacity: 0;
-webkit-transform: translateY(-25px) rotateX(90deg);
  transform: translateY(-25px) rotateX(90deg);
}

.icon-cube:hover::after {
opacity: 1;
-webkit-transform: rotateX(0);
  transform: rotateX(0);
}

/*-- facebook --*/
